class Basis {
  constructor (data = { id: '', classes: [], attributes: {} }) {
    this._id = data.id;
    this._classes = data.classes || [];
    this._attributes = data.attributes || {};
  }

  get classes () {
    return this._classes;
  }

  get attributes () {
    const attributes = {
      ...this._attributes
    };
    if (this._id) attributes.id = this._id;
    return this._attributes;
  }

  includeClasses (classes = null) {
    classes ??= this.classes;
    if (!classes || classes.length === 0) return '';
    return `class="${classes.join(' ')}"`;
  }

  includeAttributes (attributes = null) {
    attributes ??= this.attributes;
    const keys = Object.keys(attributes);
    if (keys.length === 0) return '';
    return keys.map(key => `${key}="${attributes[key]}"`).join(' ');
  }
}

export { Basis };
